"use client"

import { useState, useEffect, useMemo } from "react"
import { Sidebar } from "@/components/sidebar"
import { Header } from "@/components/header"
import { DataTable } from "@/components/data-table"
import { RecordDetailModal } from "@/components/record-detail-modal"
import { useCRMStore } from "@/lib/store"
import { sampleProtectRecords, sampleSettlementRecords } from "@/lib/sample-data"
import type { CRMRecord } from "@/lib/types"
import { filterRecords } from "@/lib/filter-utils"
import { ProtectMetrics } from "@/components/protect/protect-metrics"

export default function ProtectPage() {
  const {
    records,
    setRecords,
    getProtectRecords,
    deleteRecord,
    deleteRecords,
    isInitialized,
    setInitialized,
    hasHydrated,
  } = useCRMStore()

  // Local filter state
  const [searchQuery, setSearchQuery] = useState("")
  const [statusFilter, setStatusFilter] = useState("all")
  const [partnerFilter, setPartnerFilter] = useState("all")
  const [dateRangeStart, setDateRangeStart] = useState("")
  const [dateRangeEnd, setDateRangeEnd] = useState("")

  const [selectedRecord, setSelectedRecord] = useState<CRMRecord | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [isClient, setIsClient] = useState(false)

  useEffect(() => {
    setIsClient(true)
    if (hasHydrated && !isInitialized) {
      if (records.length === 0) {
        setRecords([...sampleProtectRecords, ...sampleSettlementRecords])
      }
      setInitialized(true)
    }
  }, [isInitialized, records.length, setRecords, setInitialized, hasHydrated])

  const protectRecords = getProtectRecords()

  const filteredRecords = useMemo(() => {
    return filterRecords(protectRecords, {
      searchQuery,
      statusFilter,
      partnerFilter,
      dateRangeStart,
      dateRangeEnd
    })
  }, [protectRecords, searchQuery, statusFilter, partnerFilter, dateRangeStart, dateRangeEnd])

  const handleViewRecord = (record: CRMRecord) => {
    setSelectedRecord(record)
    setIsModalOpen(true)
  }

  const handleSetDateRange = (start: string, end: string) => {
    setDateRangeStart(start)
    setDateRangeEnd(end)
  }

  if (!isClient) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="animate-pulse text-muted-foreground">Loading...</div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <Sidebar />
      <main className="ml-64">
        <Header title="Protect" subtitle="Manage protect requests and EMI protection cases" />

        <div className="p-6">
          <ProtectMetrics records={filteredRecords as any} />

          <DataTable
            title="Protect Records"
            records={filteredRecords}
            type="protect"
            searchQuery={searchQuery}
            setSearchQuery={setSearchQuery}
            statusFilter={statusFilter}
            setStatusFilter={setStatusFilter}
            partnerFilter={partnerFilter}
            setPartnerFilter={setPartnerFilter}
            dateRangeStart={dateRangeStart}
            dateRangeEnd={dateRangeEnd}
            setDateRange={handleSetDateRange}
            onViewRecord={handleViewRecord}
            onDeleteRecord={deleteRecord}
            onDeleteRecords={deleteRecords}
            customStatusOptions={[
              { value: "Part Payment", label: "Part Payment" },
              { value: "Skip EMI", label: "Skip EMI" },
            ]}
          />
        </div>
      </main>

      <RecordDetailModal recordId={selectedRecord?.id || null} open={isModalOpen} onClose={() => setIsModalOpen(false)} />
    </div>
  )
}
