"use client"

import { exportToExcel } from "@/lib/export-utils"

import { useState } from "react"
import { cn } from "@/lib/utils"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Search, Filter, ChevronLeft, ChevronRight, Eye, Calendar, Trash2, ChevronDown, Download } from "lucide-react"
import { Checkbox } from "@/components/ui/checkbox"
import type { CRMRecord, ProtectRecord, SettlementRecord, Status } from "@/lib/types"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog"

const statusStyles: Record<Status, string> = {
  new: "bg-blue-500/10 text-blue-500 border-blue-500/20",
  "in-progress": "bg-yellow-500/10 text-yellow-500 border-yellow-500/20",
  "follow-up": "bg-purple-500/10 text-purple-500 border-purple-500/20",
  closed: "bg-green-500/10 text-green-500 border-green-500/20",
}

const statusLabels: Record<Status, string> = {
  new: "New",
  "in-progress": "In Progress",
  "follow-up": "Follow-up",
  closed: "Closed",
}

interface DataTableProps {
  title: string
  records: CRMRecord[]
  type: "protect" | "settlement" | "nexus"
  searchQuery: string
  setSearchQuery: (query: string) => void
  statusFilter: string
  setStatusFilter: (status: string) => void
  partnerFilter: string
  setPartnerFilter: (partner: string) => void
  dpdGroupFilter?: string
  setDpdGroupFilter?: (group: string) => void
  dateRangeStart: string
  dateRangeEnd: string
  setDateRange: (start: string, end: string) => void
  onViewRecord: (record: CRMRecord) => void
  onDeleteRecord: (id: string) => void
  onDeleteRecords?: (ids: string[]) => void
  customStatusOptions?: { value: string; label: string }[]
}

export function DataTable({
  title,
  records,
  type,
  searchQuery,
  setSearchQuery,
  statusFilter,
  setStatusFilter,
  partnerFilter,
  setPartnerFilter,
  dpdGroupFilter,
  setDpdGroupFilter,
  dateRangeStart,
  dateRangeEnd,
  setDateRange,
  onViewRecord,
  onDeleteRecord,
  onDeleteRecords,
  customStatusOptions,
}: DataTableProps) {
  const [currentPage, setCurrentPage] = useState(1)
  const [recordToDelete, setRecordToDelete] = useState<string | null>(null)
  const [selectedRecords, setSelectedRecords] = useState<Set<string>>(new Set())
  const [showBulkDeleteConfirm, setShowBulkDeleteConfirm] = useState(false)
  const [pageSize, setPageSize] = useState(10)

  const totalPages = Math.ceil(records.length / pageSize)
  const paginatedRecords = records.slice((currentPage - 1) * pageSize, currentPage * pageSize)

  const handleFilterChange = () => {
    setCurrentPage(1)
    setSelectedRecords(new Set())
  }

  const toggleSelectAll = () => {
    // If all on current page are selected, deselect them
    const allOnPageSelected = paginatedRecords.every(r => selectedRecords.has(r.id))

    if (allOnPageSelected) {
      const newSelected = new Set(selectedRecords)
      paginatedRecords.forEach(r => newSelected.delete(r.id))
      setSelectedRecords(newSelected)
    } else {
      // Select all on current page
      const newSelected = new Set(selectedRecords)
      paginatedRecords.forEach(r => newSelected.add(r.id))
      setSelectedRecords(newSelected)
    }
  }

  const selectAllAcrossPages = () => {
    setSelectedRecords(new Set(records.map(r => r.id)))
  }

  const toggleSelectRecord = (id: string) => {
    const newSelected = new Set(selectedRecords)
    if (newSelected.has(id)) {
      newSelected.delete(id)
    } else {
      newSelected.add(id)
    }
    setSelectedRecords(newSelected)
  }

  const handleBulkDelete = () => {
    console.log("handleBulkDelete called", { selectedRecords: Array.from(selectedRecords), onDeleteRecords: !!onDeleteRecords })
    if (onDeleteRecords) {
      onDeleteRecords(Array.from(selectedRecords))
      setSelectedRecords(new Set())
      setShowBulkDeleteConfirm(false)
    }
  }

  const handleExport = () => {
    exportToExcel(records as CRMRecord[], `crm-export-${new Date().toISOString().split('T')[0]}`)
  }

  return (
    <Card className="bg-card border-border">
      <CardHeader className="pb-4">
        <div className="flex flex-col gap-4">
          <div className="flex items-center justify-between">
            <CardTitle className="text-lg font-semibold text-foreground">{title}</CardTitle>
            <div className="flex items-center gap-2">
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="outline" size="sm" className="h-8">
                    Actions <ChevronDown className="ml-2 h-4 w-4" />
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuItem onClick={handleExport}>
                    <Download className="mr-2 h-4 w-4" />
                    Export All ({records.length})
                  </DropdownMenuItem>
                  {selectedRecords.size > 0 && (
                    <>
                      <DropdownMenuSeparator />
                      <DropdownMenuItem onClick={() => {
                        const selectedData = records.filter(r => selectedRecords.has(r.id))
                        exportToExcel(selectedData as CRMRecord[], `crm-export-selected-${new Date().toISOString().split('T')[0]}`)
                      }}>
                        <Download className="mr-2 h-4 w-4" />
                        Export Selected ({selectedRecords.size})
                      </DropdownMenuItem>
                      {onDeleteRecords && (
                        <DropdownMenuItem
                          onClick={() => setShowBulkDeleteConfirm(true)}
                          className="text-red-600 focus:text-red-600"
                        >
                          <Trash2 className="mr-2 h-4 w-4" />
                          Delete Selected ({selectedRecords.size})
                        </DropdownMenuItem>
                      )}
                    </>
                  )}
                </DropdownMenuContent>
              </DropdownMenu>
              <span className="text-sm text-muted-foreground">{records.length} records</span>
            </div>
          </div>

          {/* Filters */}
          <div className="flex flex-wrap items-center gap-3">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
              <Input
                placeholder="Search by name, mobile..."
                value={searchQuery}
                onChange={(e) => {
                  setSearchQuery(e.target.value)
                  handleFilterChange()
                }}
                className="w-[250px] pl-9 bg-muted/50 border-border text-sm"
              />
            </div>

            <Select
              value={statusFilter}
              onValueChange={(v) => {
                setStatusFilter(v)
                handleFilterChange()
              }}
            >
              <SelectTrigger className="w-[150px] bg-muted/50 border-border text-sm">
                <Filter className="mr-2 h-3 w-3" />
                <SelectValue placeholder="Status" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All {customStatusOptions ? "Stages" : "Status"}</SelectItem>
                {customStatusOptions ? (
                  customStatusOptions.map((option) => (
                    <SelectItem key={option.value} value={option.value}>
                      {option.label}
                    </SelectItem>
                  ))
                ) : (
                  <>
                    <SelectItem value="new">New</SelectItem>
                    <SelectItem value="in-progress">In Progress</SelectItem>
                    <SelectItem value="follow-up">Follow-up</SelectItem>
                    <SelectItem value="closed">Closed</SelectItem>
                  </>
                )}
              </SelectContent>
            </Select>

            <Select
              value={partnerFilter}
              onValueChange={(v) => {
                setPartnerFilter(v)
                handleFilterChange()
              }}
            >
              <SelectTrigger className="w-[150px] bg-muted/50 border-border text-sm">
                <SelectValue placeholder="Partner" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Partners</SelectItem>
                <SelectItem value="sayyam">Sayyam</SelectItem>
                <SelectItem value="snapmint">Snapmint</SelectItem>
              </SelectContent>
            </Select>

            {type === "protect" && setDpdGroupFilter && (
              <Select
                value={dpdGroupFilter || "all"}
                onValueChange={(v) => {
                  setDpdGroupFilter(v)
                  handleFilterChange()
                }}
              >
                <SelectTrigger className="w-[150px] bg-muted/50 border-border text-sm">
                  <SelectValue placeholder="DPD Group" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All DPD</SelectItem>
                  <SelectItem value="0-30">0-30</SelectItem>
                  <SelectItem value="31-60">31-60</SelectItem>
                  <SelectItem value="61-90">61-90</SelectItem>
                  <SelectItem value="91-180">91-180</SelectItem>
                  <SelectItem value="180+">180+</SelectItem>
                </SelectContent>
              </Select>
            )}

            <div className="flex items-center gap-2">
              <Calendar className="h-4 w-4 text-muted-foreground" />
              <Input
                type="date"
                value={dateRangeStart}
                onChange={(e) => {
                  setDateRange(e.target.value, dateRangeEnd)
                  handleFilterChange()
                }}
                className="w-[140px] bg-muted/50 border-border text-sm"
              />
              <span className="text-muted-foreground">to</span>
              <Input
                type="date"
                value={dateRangeEnd}
                onChange={(e) => {
                  setDateRange(dateRangeStart, e.target.value)
                  handleFilterChange()
                }}
                className="w-[140px] bg-muted/50 border-border text-sm"
              />
            </div>
          </div>
        </div>
      </CardHeader>

      <CardContent>
        {selectedRecords.size > 0 && selectedRecords.size < records.length &&
          paginatedRecords.every(r => selectedRecords.has(r.id)) && (
            <div className="bg-muted/50 p-2 text-center text-sm text-muted-foreground border-b border-border mb-4 rounded-md">
              All {paginatedRecords.length} records on this page are selected.{" "}
              <button
                onClick={selectAllAcrossPages}
                className="text-primary hover:underline font-medium"
              >
                Select all {records.length} records
              </button>
            </div>
          )}

        <div className="rounded-lg border border-border overflow-hidden">
          <div className="overflow-x-auto">
            <Table className="table-fixed">
              <TableHeader>
                <TableRow className="bg-muted/30 hover:bg-muted/30">
                  <TableHead className="w-[40px]">
                    <Checkbox
                      checked={paginatedRecords.length > 0 && paginatedRecords.every(r => selectedRecords.has(r.id))}
                      onCheckedChange={toggleSelectAll}
                      aria-label="Select all"
                    />
                  </TableHead>
                  {type === "protect" ? (
                    <>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Purchase Date
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Form Filled
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[150px]">
                        Name
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Mobile
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Plan
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[150px]">
                        Institution
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[120px]">
                        Account No
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap text-right w-[100px]">
                        EMI Amt
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap text-center w-[60px]">
                        DPD
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        DPD Group
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Status
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Part Payment
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Partner
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Stage
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[300px]">
                        Remarks
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[80px]">Action</TableHead>
                    </>
                  ) : type === "nexus" ? (
                    <>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        User Id
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[150px]">
                        User
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Mobile Number
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[200px]">
                        Email
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Purchase Date
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Form Filled
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Status
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[80px]">Action</TableHead>
                    </>
                  ) : (
                    <>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Purchase Date
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Form Filled
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[150px]">
                        Name
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Mobile
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[200px]">
                        Lender
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[120px]">
                        Loan Acc No
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap text-right w-[100px]">
                        Due Amt
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Due Date
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap text-center w-[80px]">
                        Bounced
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap text-center w-[80px]">
                        Legal
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap text-right w-[100px]">
                        Recommended
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Status
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Part Payment
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Partner
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs whitespace-nowrap w-[100px]">
                        Stage
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[300px]">
                        Remarks
                      </TableHead>
                      <TableHead className="text-muted-foreground font-medium text-xs w-[80px]">Action</TableHead>
                    </>
                  )}
                </TableRow>
              </TableHeader>
              <TableBody>
                {paginatedRecords.length === 0 ? (
                  <TableRow>
                    <TableCell
                      colSpan={type === "protect" ? 16 : 17}
                      className="text-center py-8 text-muted-foreground"
                    >
                      No records found matching your criteria
                    </TableCell>
                  </TableRow>
                ) : (
                  paginatedRecords.map((record) => (
                    <TableRow
                      key={record.id}
                      className="hover:bg-muted/20 cursor-pointer"
                      onClick={() => onViewRecord(record)}
                    >
                      <TableCell onClick={(e) => e.stopPropagation()}>
                        <Checkbox
                          checked={selectedRecords.has(record.id)}
                          onCheckedChange={() => toggleSelectRecord(record.id)}
                          aria-label={`Select ${record.name}`}
                        />
                      </TableCell>
                      {type === "nexus" ? (
                        <>
                          <TableCell className="text-xs font-mono whitespace-nowrap">
                            {(record as any).userId || "-"}
                          </TableCell>
                          <TableCell className="text-xs font-medium w-[150px] truncate" title={record.name}>{record.name}</TableCell>
                          <TableCell className="text-xs whitespace-nowrap font-mono truncate">{record.mobileNumber}</TableCell>
                          <TableCell className="text-xs truncate max-w-[200px]" title={(record as any).email}>
                            {(record as any).email || "-"}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap">
                            {(record as any).nexusPurchaseDate}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap">
                            {record.formFilledDate ? (
                              <Badge variant="outline" className="bg-green-500/10 text-green-500 border-green-500/20">
                                Yes
                              </Badge>
                            ) : (
                              <Badge variant="outline" className="bg-red-500/10 text-red-500 border-red-500/20">
                                No
                              </Badge>
                            )}
                          </TableCell>

                          <TableCell>
                            <Badge variant="outline" className={cn("text-xs whitespace-nowrap", statusStyles[record.status])}>
                              {statusLabels[record.status]}
                            </Badge>
                          </TableCell>
                          <TableCell>
                            <ActionButtons
                              record={record}
                              onView={onViewRecord}
                              onDelete={setRecordToDelete}
                            />
                          </TableCell>
                        </>
                      ) : type === "protect" ? (
                        <>
                          <TableCell className="text-xs whitespace-nowrap">
                            {(record as ProtectRecord).nexusPurchaseDate}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap">
                            {(record as ProtectRecord).formFilledDate}
                          </TableCell>
                          <TableCell className="text-xs font-medium w-[150px] truncate" title={record.name}>{record.name}</TableCell>
                          <TableCell className="text-xs whitespace-nowrap font-mono truncate">{record.mobileNumber}</TableCell>
                          <TableCell className="text-xs whitespace-nowrap truncate" title={(record as ProtectRecord).plan}>{(record as ProtectRecord).plan}</TableCell>
                          <TableCell className="text-xs w-[150px] truncate" title={(record as ProtectRecord).institution}>
                            {(record as ProtectRecord).institution}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap font-mono truncate" title={(record as ProtectRecord).accountNumber}>
                            {(record as ProtectRecord).accountNumber}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap text-right font-mono">
                            {(record as ProtectRecord).emiAmount.toLocaleString()}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap text-center font-mono">
                            {(record as ProtectRecord).currentDpd}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap">
                            <Badge variant="outline" className="text-xs">
                              {(record as ProtectRecord).dpdGroup}
                            </Badge>
                          </TableCell>
                          <TableCell>
                            <Badge variant="outline" className={cn("text-xs whitespace-nowrap", statusStyles[record.status])}>
                              {statusLabels[record.status]}
                            </Badge>
                          </TableCell>
                          <TableCell className="text-xs font-mono">
                            {(record as ProtectRecord).partPaymentAmount ? `${(record as ProtectRecord).partPaymentAmount?.toLocaleString()}` : "-"}
                          </TableCell>
                          <TableCell className="text-xs capitalize whitespace-nowrap truncate" title={record.partner}>{record.partner}</TableCell>
                          <TableCell className="text-xs whitespace-nowrap truncate" title={record.stage || "-"}>{record.stage || "-"}</TableCell>
                          <TableCell className="text-xs whitespace-pre-wrap break-words py-2" title={record.remarks?.length ? record.remarks[record.remarks.length - 1].text : "-"}>
                            {record.remarks?.length ? record.remarks[record.remarks.length - 1].text : "-"}
                          </TableCell>
                          <TableCell>
                            <ActionButtons
                              record={record}
                              onView={onViewRecord}
                              onDelete={setRecordToDelete}
                            />
                          </TableCell>
                        </>
                      ) : (
                        <>
                          <TableCell className="text-xs whitespace-nowrap">
                            {(record as SettlementRecord).nexusPurchaseDate}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap">
                            {(record as SettlementRecord).formFilledDate}
                          </TableCell>
                          <TableCell className="text-xs font-medium w-[150px] truncate" title={record.name}>{record.name}</TableCell>
                          <TableCell className="text-xs whitespace-nowrap font-mono truncate">{record.mobileNumber}</TableCell>
                          <TableCell className="text-xs w-[200px] truncate" title={(record as SettlementRecord).lenderName}>
                            {(record as SettlementRecord).lenderName}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap font-mono truncate" title={(record as SettlementRecord).loanAccNo}>
                            {(record as SettlementRecord).loanAccNo}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap text-right font-mono">
                            {(record as SettlementRecord).dueAmt.toLocaleString()}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap">
                            {(record as SettlementRecord).dueDate}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap text-center">
                            {(record as SettlementRecord).isEmiBounced ? (
                              <Badge variant="destructive" className="text-xs">
                                Yes
                              </Badge>
                            ) : (
                              <span className="text-muted-foreground">No</span>
                            )}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap text-center">
                            {(record as SettlementRecord).isLegalNotice ? (
                              <Badge variant="destructive" className="text-xs">
                                Yes
                              </Badge>
                            ) : (
                              <span className="text-muted-foreground">No</span>
                            )}
                          </TableCell>
                          <TableCell className="text-xs whitespace-nowrap text-right font-mono">
                            {(record as SettlementRecord).recommendedAmt.toLocaleString()}
                          </TableCell>
                          <TableCell>
                            <Badge variant="outline" className={cn("text-xs whitespace-nowrap", statusStyles[record.status])}>
                              {statusLabels[record.status]}
                            </Badge>
                          </TableCell>
                          <TableCell className="text-xs font-mono">
                            {(record as SettlementRecord).partPaymentAmount ? `${(record as SettlementRecord).partPaymentAmount?.toLocaleString()}` : "-"}
                          </TableCell>
                          <TableCell className="text-xs capitalize whitespace-nowrap truncate" title={record.partner}>{record.partner}</TableCell>
                          <TableCell className="text-xs whitespace-nowrap truncate" title={record.stage || "-"}>{record.stage || "-"}</TableCell>
                          <TableCell className="text-xs whitespace-pre-wrap break-words py-2" title={record.remarks?.length ? record.remarks[record.remarks.length - 1].text : "-"}>
                            {record.remarks?.length ? record.remarks[record.remarks.length - 1].text : "-"}
                          </TableCell>
                          <TableCell>
                            <ActionButtons
                              record={record}
                              onView={onViewRecord}
                              onDelete={setRecordToDelete}
                            />
                          </TableCell>
                        </>
                      )}
                    </TableRow>
                  ))
                )}
              </TableBody>
            </Table>
          </div>
        </div>

        {/* Pagination */}
        {totalPages > 0 && (
          <div className="flex items-center justify-between pt-4">
            <div className="flex items-center gap-2">
              <p className="text-sm text-muted-foreground">
                Showing {records.length === 0 ? 0 : (currentPage - 1) * pageSize + 1} to{" "}
                {Math.min(currentPage * pageSize, records.length)} of {records.length}
              </p>
              <Select
                value={pageSize.toString()}
                onValueChange={(v) => {
                  setPageSize(Number(v))
                  setCurrentPage(1)
                }}
              >
                <SelectTrigger className="h-8 w-[70px]">
                  <SelectValue placeholder={pageSize} />
                </SelectTrigger>
                <SelectContent side="top">
                  {[10, 20, 50, 100].map((size) => (
                    <SelectItem key={size} value={size.toString()}>
                      {size}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div className="flex items-center gap-1">
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
                disabled={currentPage === 1}
                className="h-8 w-8 p-0"
              >
                <ChevronLeft className="h-4 w-4" />
              </Button>
              {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
                let page = i + 1
                if (totalPages > 5) {
                  if (currentPage > 3) page = currentPage - 2 + i
                  if (currentPage > totalPages - 2) page = totalPages - 4 + i
                }
                return (
                  <Button
                    key={page}
                    variant={currentPage === page ? "default" : "outline"}
                    size="sm"
                    onClick={() => setCurrentPage(page)}
                    className="h-8 w-8 p-0"
                  >
                    {page}
                  </Button>
                )
              })}
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
                disabled={currentPage === totalPages || totalPages === 0}
                className="h-8 w-8 p-0"
              >
                <ChevronRight className="h-4 w-4" />
              </Button>
            </div>
          </div>
        )}
      </CardContent>

      <AlertDialog open={!!recordToDelete} onOpenChange={(open: boolean) => !open && setRecordToDelete(null)}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Are you sure?</AlertDialogTitle>
            <AlertDialogDescription>
              This action cannot be undone. This will permanently delete the record from the database.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction
              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
              onClick={() => {
                if (recordToDelete) {
                  onDeleteRecord(recordToDelete)
                  setRecordToDelete(null)
                }
              }}
            >
              Delete
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      <AlertDialog open={showBulkDeleteConfirm} onOpenChange={setShowBulkDeleteConfirm}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Are you sure?</AlertDialogTitle>
            <AlertDialogDescription>
              This action cannot be undone. This will permanently delete {selectedRecords.size} selected records from the database.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction
              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
              onClick={handleBulkDelete}
            >
              Delete {selectedRecords.size} Records
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </Card >
  )
}

function ActionButtons({
  record,
  onView,
  onDelete,
}: {
  record: CRMRecord
  onView: (r: CRMRecord) => void
  onDelete: (id: string) => void
}) {
  return (
    <div className="flex items-center gap-1">
      <Button
        variant="ghost"
        size="icon"
        className="h-7 w-7"
        onClick={(e: React.MouseEvent) => {
          e.stopPropagation()
          onView(record)
        }}
      >
        <Eye className="h-4 w-4" />
      </Button>
      <Button
        variant="ghost"
        size="icon"
        className="h-7 w-7 text-destructive hover:text-destructive hover:bg-destructive/10"
        onClick={(e: React.MouseEvent) => {
          e.stopPropagation()
          onDelete(record.id)
        }}
      >
        <Trash2 className="h-4 w-4" />
      </Button>
    </div>
  )
}
